<template><div class="content"><h1 class="post-title">纵观烟火和习惯于向往美好以后我的变化<a class="header-anchor" id="%E7%BA%B5%E8%A7%82%E7%83%9F%E7%81%AB%E5%92%8C%E4%B9%A0%E6%83%AF%E4%BA%8E%E5%90%91%E5%BE%80%E7%BE%8E%E5%A5%BD%E4%BB%A5%E5%90%8E%E6%88%91%E7%9A%84%E5%8F%98%E5%8C%96" href="#%E7%BA%B5%E8%A7%82%E7%83%9F%E7%81%AB%E5%92%8C%E4%B9%A0%E6%83%AF%E4%BA%8E%E5%90%91%E5%BE%80%E7%BE%8E%E5%A5%BD%E4%BB%A5%E5%90%8E%E6%88%91%E7%9A%84%E5%8F%98%E5%8C%96">#</a></h1><div class="metabar"><div class="metabar-item">2023/01/01</div><div class="metabar-item">思想</div><div class="metabar-item">约 5 分钟读完</div></div>
<p>时间快到每一次经历这样的总结之时都让我不由得发出感叹。</p>
<p>值得注意的是，从最初写这一个版本的博客（2019 年）开始，几乎每一年我的心理变化都是巨大的，或许对于有些人来说并非如此。2022 年无疑是近些年来变化最大的一年，而到来的 2023 年则很有可能是我真正脱离苦海的一年。</p>
<p>我并不能向自己保证，如此抽象的一个「新的开始」真的可以作为自我完全刷新的契机。我一直都在极力避免自己失望，所以也一直不敢给自己类似的承诺，毕竟已经失败了太多次。</p>
<p>从前的我敏感又怯懦，矫情又造作，借助于治疗，我一直在改变，在变得难以伤害，变得更加勇于重拳出击，更重要的是，变得更加自我，也更加专注于自己的利益。</p>
<p>2022 年的 5 月，本身是为了解决我类似 ADD 的症状，在饱受类似于“ADD 不是小孩子得的病吗？”之类的怀疑下，前往医院准备咨询。谁知和医生聊经历的时候，聊着聊着就哭了起来，结果医生让我去做了些测试，诊断出重度抑郁。</p>
<p>其实抑郁就像计算机，人多了以后，无论是好是坏，都变得极为廉价，不值得一提。我想这是好事，但在某种程度上又让我无比惋惜。对于二者都是这样。</p>
<p>在吃药以后，虽然我没有察觉我的行为有多少明显的诡异，但又感觉无论是在心理上还是行动上有了变化。这让我感觉每个日子都过得轻飘飘的，宛如浮在天空中。</p>
<p>即使是这样，我也能天天看到整日视乐色为珍宝的人，以出乎意料的自我封闭能力，贬低他人的同时增加自己的锐气，在此不过多赘述。</p>
<p>只想说这些苦痛折磨，也许这一生也就只有这一段时间有吧。2023 年，我终于迎来了摆脱的机会。</p>
<p>烟火即将熄灭之时，我感到无比的庆幸。2023 年的到来，或许是最近些年最好的喜讯。2023 年或许并不会如我所期望的那样完美，却在客观事实上已经注定了我与痛苦之间的距离——终将变为九霄云外的往事。</p>
</div></template>